@charset "UTF-8";
/* ================================
	Frontend CSS.

	Index:
	r01 - Placeholders
	r02 - Global
	r03 - Utilities
	r04 - Components
	r05 - Layouts
===================================*/
.rt-d-flex {
  display: flex;
}

.rt-flex-wrap {
  flex-wrap: wrap;
}

.rt-justify-content-center {
  justify-content: center;
}

.rt-align-items-center {
  align-items: center;
}

.rt-absolute-content, .rt-relative-content, .rt-static-content {
  position: relative;
}

.rt-primary-color {
  color: var(--color-primary);
}

.rt-primary-color-bg {
  background-color: var(--color-primary);
}

.rt-white-color {
  color: var(--color-white);
}

.rt-white-color-bg {
  background-color: var(--color-white);
}

.rt-black-color {
  color: var(--color-black);
}

.rt-black-color-bg {
  background-color: var(--color-black);
}

.rt-mb-0 {
  margin-bottom: 0;
}

/**
 * Global CSS.
 */
:root {
  --color-primary: #7D51F8;
  --color-white: #fff;
  --color-black: #122428;
  --color-body: #6B6B6B;
  --color-gray: #FAF9FF;
  --font-body: "DM Sans", sans-serif;
  --font-title: "Plus Jakarta Sans", sans-serif;
  --border-radius-xs: 4px;
  --border-radius-sm: 6px;
  --border-radius-md: 10px;
  --border-radius-lg: 15px;
  --border-radius-xl: 20px;
}

/* ===========================
// Typography
==============================*/
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-body);
  font-size: 16px;
  color: var(--color-body);
  font-weight: 400;
}

p {
  font-family: var(--font-body);
  font-weight: 400;
  font-size: 16px;
  line-height: 30px;
  color: var(--color-body);
  margin-bottom: 15px;
}

/*==== heading fonts setup ====*/
h1,
h2,
h3,
h4,
h5,
h6 {
  color: var(--color-black);
  font-family: var(--font-title);
  line-height: 1.5;
  font-weight: 500;
}

h1 {
  font-size: 48px;
  line-height: 1.23;
}
@media (max-width: 767px) {
  h1 {
    font-size: 36px;
  }
}

h2 {
  font-size: 40px;
}

h3 {
  font-size: 32px;
}

h4 {
  font-size: 26px;
}

h5 {
  font-size: 22px;
}

h6 {
  font-size: 18px;
}

