/*======================================
// pre-loader
=====================================*/
#preloader {
  background: url("../../media/preloader.gif") rgba(255, 255, 255, 0.99) no-repeat scroll center center;
  height: 100%;
  left: 0;
  overflow: visible;
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 9999999;
}

/*======================================
// Back-to-top
=====================================*/
.ayo-bc-totop {
  position: fixed;
  z-index: 15;
  bottom: 46px;
  right: 40px;
  text-decoration: none;
  width: 40px;
  height: 40px;
  border: 1px solid var(--color-primary);
  border-radius: 20px;
  background-color: #fff;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  transform: translateY(120px);
}

.ayo-bc-totop .progress-wrapper {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  overflow: hidden;
  display: block;
  border-radius: 50%;
}

.ayo-bc-totop .progress {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 0;
  background-color: var(--color-primary);
}

.ayo-bc-totop.active {
  transform: translateY(0);
}

.ayo-bc-totop.active .bc-svg {
  transform: rotate(0deg) translateX(0px);
  transition: all 0.3s ease;
}

.ayo-bc-totop .bc-svg {
  color: var(--color-black);
}

