/*=======================================================================
    Button
=========================================================================*/
.sasxo-button .sasxo-btn {
  -webkit-appearance: none;
  cursor: pointer;
  vertical-align: top;
  font-size: 15px;
  font-weight: 700;
  border-radius: 30px;
  white-space: nowrap;
  height: 50px;
  max-width: 100%;
  border-width: 2px !important;
  border-style: solid;
  padding: 12px 30px;
  animation: animation_back_down 0.3s ease forwards;
  position: relative;
  color: var(--color-white);
  background-color: var(--color-primary);
  border-color: var(--color-primary);
  align-items: center;
  display: inline-flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: reverse;
  flex-direction: row-reverse;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
}
.sasxo-button .sasxo-btn:hover {
  color: var(--color-white);
  background-color: var(--color-black);
  border-color: var(--color-black);
  animation: animation_jump_up 0.3s ease forwards;
}
.sasxo-button .sasxo-btn:hover .icon {
  left: 0px;
  margin-left: 9px;
  margin-right: -14px;
  opacity: 1;
}
.sasxo-button .sasxo-btn:hover .button-text {
  margin-left: -9px;
}
.sasxo-button .sasxo-btn .icon {
  position: relative;
  left: -15px;
  margin-right: -15px;
  margin-left: 0;
  opacity: 0;
  transition: all ease 0.3s;
  float: none;
  margin-top: 0;
  font-size: 15px;
  height: 19px;
  line-height: 19px;
  width: 19px;
  text-align: left;
  display: inline-block;
  vertical-align: bottom;
  white-space: normal;
  text-decoration: inherit;
}
.sasxo-button .sasxo-btn .button-text {
  transition: all ease 0.3s;
  display: inline-block;
  vertical-align: bottom;
  white-space: normal;
  text-decoration: inherit;
}
.sasxo-button .sasxo-btn .button-text .title {
  color: var(--color-white);
}
.sasxo-button .sasxo-btn.style-2 {
  background-color: #F7F4FF;
  border-color: #F7F4FF;
  color: var(--color-primary);
}
.sasxo-button .sasxo-btn.style-2 .button-text .title {
  color: var(--color-primary);
}
.sasxo-button .sasxo-btn.style-2:hover {
  color: var(--color-white);
  background-color: var(--color-primary);
  border-color: var(--color-primary);
}
.sasxo-button .sasxo-btn.style-2:hover .button-text .title {
  color: var(--color-white);
}

.button-list ul {
  display: flex;
  gap: 10px;
}
.button-list ul li .brand-btn {
  border: 1px solid var(--color-white);
  border-radius: 5000px;
  background-color: transparent;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  transition: all 0.4s ease-in-out;
  z-index: 1;
  padding: 8px 20px;
}
.button-list ul li .brand-btn:hover {
  border-color: var(--color-white);
  background-color: var(--color-white);
}
.button-list ul li .brand-btn:hover .brand-app i {
  color: var(--color-black);
}
.button-list ul li .brand-btn:hover .content .label {
  color: var(--color-black);
}
.button-list ul li .brand-btn:hover .content .text {
  color: var(--color-black);
}
.button-list ul li .brand-btn .brand-app i {
  color: var(--color-white);
  font-size: 18px;
  transition: all 0.4s ease-in-out;
  margin-right: 7px;
}
.button-list ul li .brand-btn .brand-app .fa-apple {
  font-size: 22px;
}
.button-list ul li .brand-btn .content .label {
  font-size: 14px;
  line-height: 20px;
  color: var(--color-white);
  transition: all 0.4s ease-in-out;
  line-height: 1;
}
.button-list ul li .brand-btn .content .text {
  font-family: var(--font-title);
  font-weight: 700;
  font-size: 18px;
  color: var(--color-white);
  transition: all 0.4s ease-in-out;
}

.banner-button .banner-btn {
  color: var(--color-primary);
  background-color: var(--color-white);
  border: 1px solid var(--color-white);
  font-size: 20px;
  font-weight: 700;
  line-height: normal;
  text-transform: capitalize;
  text-align: center;
  display: inline-block;
  padding: 15px 24px 17px;
  border-radius: 3px;
  transition: all 0.35s ease-in-out;
}
.banner-button .banner-btn:hover {
  background-color: transparent;
  border-color: var(--color-white);
  color: var(--color-white);
}
.banner-button .banner-btn.style-2 {
  background-color: var(--color-primary);
  color: var(--color-white);
  border: 1px solid var(--color-primary);
}
.banner-button .banner-btn.style-2:hover {
  background-color: transparent;
  color: var(--color-primary);
  border-color: var(--color-primary);
}

.button-style-2 .btn-style-2 {
  background-color: transparent;
  border: 1px solid var(--color-primary);
  color: var(--color-primary);
  font-size: 16px;
  font-weight: 700;
  line-height: normal;
  text-transform: capitalize;
  text-align: center;
  display: inline-block;
  padding: 12px 24px 13px;
  border-radius: 3px;
  transition: all 0.35s ease-in-out;
}
.button-style-2 .btn-style-2:hover {
  background-color: var(--color-primary);
  border-color: var(--color-primary);
  color: var(--color-white);
}
.button-style-2 .btn-style-2.fill-btn {
  background-color: var(--color-primary);
  color: var(--color-white);
}
.button-style-2 .btn-style-2.fill-btn:hover {
  background-color: var(--color-black);
  color: var(--color-white);
  border-color: var(--color-black);
}
.button-style-2 .btn-style-2.radius-30 {
  border-radius: 30px;
}

.sasxo-button-2 .sasxo-btn-2 {
  color: var(--color-white);
  font-size: 16px;
  font-weight: 700;
  line-height: normal;
  text-transform: capitalize;
  text-align: center;
  display: inline-block;
  padding: 12px 24px 13px;
  border-radius: 3px;
  transition: all 0.3s;
  position: relative;
  background-color: var(--color-primary);
  border-color: var(--color-primary);
  overflow: hidden;
  max-width: 100%;
  z-index: 1;
}
.sasxo-button-2 .sasxo-btn-2:hover {
  color: var(--color-white);
}
.sasxo-button-2 .sasxo-btn-2:hover span {
  opacity: 0;
  transform: translate(0, -25%);
}
.sasxo-button-2 .sasxo-btn-2:hover::after {
  opacity: 1;
  transform: translate(0, 0);
}
.sasxo-button-2 .sasxo-btn-2::after {
  position: absolute;
  top: 0;
  left: 0;
  background-color: var(--color-black);
  content: attr(data-text);
  opacity: 0;
  transform: translate(0, 25%);
  transition: transform 0.3s, opacity 0.3s, -webkit-transform 0.3s;
  transition-timing-function: cubic-bezier(0.2, 1, 0.3, 1);
  padding: 12px 24px 13px;
  border-radius: 3px;
}
.sasxo-button-2 .sasxo-btn-2 span {
  display: block;
  vertical-align: middle;
  transition: transform 0.3s, opacity 0.3s, -webkit-transform 0.3s;
  transition-timing-function: cubic-bezier(0.2, 1, 0.3, 1);
}

.sasxo-btn-3 {
  background-color: var(--color-primary);
  font-size: 16px;
  font-weight: 700;
  line-height: normal;
  text-transform: capitalize;
  text-align: center;
  display: inline-block;
  padding: 12px 24px 13px;
  transition: 0.3s;
  position: relative;
  overflow: hidden;
  z-index: 0;
}
.sasxo-btn-3.style-2 {
  background-color: transparent;
  border: 1px solid var(--color-primary);
}
.sasxo-btn-3.style-2 .btn-wrap .btn-text1 {
  color: var(--color-primary);
}
.sasxo-btn-3.style-2:hover {
  background-color: var(--color-primary);
  border-color: var(--color-primary);
}
.sasxo-btn-3 span {
  position: relative;
  z-index: 1;
  transition: opacity 0.6s, transform 0.8s;
  transition-timing-function: cubic-bezier(0.15, 0.85, 0.31, 1);
}
.sasxo-btn-3 span:nth-child(2) {
  display: inline-block;
  position: absolute;
  width: 100%;
  top: 50%;
  left: 50%;
  transform: translate(-50%, 80%);
  opacity: 0;
}
.sasxo-btn-3:hover span:nth-child(2) {
  transform: translate(-50%, 100%);
  opacity: 1;
  color: var(--color-white);
}
.sasxo-btn-3:hover .btn-wrap {
  transform: translateY(-150%);
}
.sasxo-btn-3:hover .btn-wrap .btn-text1 {
  opacity: 0;
}
.sasxo-btn-3 .btn-wrap {
  display: inline-block;
}
.sasxo-btn-3 .btn-wrap .btn-text1 {
  color: var(--color-white);
  transition: 0.3s;
}

@keyframes animation_back_down {
  0% {
    transform: translateY(-3px);
  }
  100% {
    transform: translateY(0);
  }
}
@keyframes animation_jump_up {
  0% {
    transform: translateY(0px);
  }
  100% {
    transform: translateY(-3px);
  }
}
