  width: 100%;
}

.rt-main-menu .menu .menu-item.menu-item-has-children .sub-menu a::after {
  content: "";
  height: 1px;
  width: 0;
  position: absolute;
  left: 0;
  bottom: -1px;
  transition: all 0.3s ease-in-out;
