/*=======================
  // template-search
========================*/
.template-search {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 280px;
  background: #FFFFFF;
  box-shadow: 0px 0px 50px rgba(0, 0, 0, 0.1);
